<script>
  import HeaderPublc from '../public/herder.vue'
  import saechFirst from '../index.vue'
  import Public from '../../vuex/public.js'
  import {mapState, mapActions} from 'vuex'
  import makImg from '../../../static/img/icon/icon_car_nor.png'
  export default {
    name: 'my-map',
    created () {
      // 查看本页面路由
      this.check = this.$route.name
     // 查看有无登陆
      var userData = Public.publicLo.sessionStorageGet('user')
      if (userData) {
        // 转换对象
        userData = JSON.parse(userData)
        // 渲染
        this.user = {
          nickName: userData.nickName,
          headImg: userData.headImg,
          id: userData.userId
        }
      }
    },
    data () {
      return {
        makImg: makImg,
        mapDate: null,
        platform: null,
        behavior: null,
        defaultLayers: null,
        ui: null,
        // 上次列表
        list2: [],
        ULlist: {
          first: {
            lat: null, // 经度
            lng: null, // 纬度
            ymd: null,   // 日期
            hms: null,   // 时间
            location: null // 地址信息
          }, // 开始地址
          list: [],   // 中间地址
          last: {
            lat: null, // 经度
            lng: null, // 纬度
            ymd: null,   // 日期
            hms: null,   // 时间
            location: null // 地址信息
          }, //  第二次地址
          listCnut: null
        },
        dataChart: {
          containerId: null,
          transportStatus: null,
          deviceNumber: null,
          startTime: null
        }, // 基础信息
//        list: [
//          {
//            "containerId":"XALJLJJGE",// 柜号/车牌号
//            "transportStatus": 1,// 运输状态
//            "startPosition": {// 起始地
//              "country": "123",
//              "state": "124",
//              "city": "125",
//              "address": "XXXXX"
//            },
//            "endPosition": {// 目的地
//              "country": "126",
//              "state": "127",
//              "city": "128",
//              "address": "XXXXX"
//            },
//            "tracks": [
//              {
//                "lat": 39.6395721447,// 经度
//                "lng": 116.4551209669,// 纬度
//                "time": 1511853359,// 时间
//                "location": "XX国XX省XX市XX街道"//地址信息
//              },
//              {
//                "lat": 39.0533126071,
//                "lng": 117.2186173176,
//                "time": 1511853359,// 时间
//                "location": "XX国XX省XX市XX街道"
//              },
//              {
//                "lat": 39.0660959351,
//                "lng": 116.0265903393,
//                "time": 1511853359,// 时间
//                "location": "XX国XX省XX市XX街道"
//              }
//            ]
//          }
//        ],
        bubble: null,
        routeInstructionsContainer: null,
        input: null, // 设备id
        firstSech: true,  // 是否第一次搜索
        //
        clickText: '共10个位置',
        clickStatus: false,
        showBox: false,
        user: {
          nickName: null,
          headImg: null,
          id: null
        }
      }
    },
    methods: {
      ...mapActions({
        mapListpost: 'mapListFn',
        checkID: 'checkId',
        checkNumber: 'checkNumber'
      }),
      // 渲染地图
      mapFn (map, parm) {
        if (parm) {
          map.setCenter({lat: parm.lat, lng: parm.lng})
        } else {
          map.setCenter({lat: 52.5160, lng: 13.3779})
        }
        map.setZoom(13)
        map.setBaseLayer(this.defaultLayers.normal.traffic)
      },
      mapService () {
        // 选取地图容器
        var mapContainer = document.getElementById('map')
//        this.routeInstructionsContainer = document.getElementById('panel')
        // 地图配置：初始化与地图平台数据
        this.platform = new H.service.Platform({
          app_id: 'X6Xrn8u6EXOVQmSMPlIP',
          app_code: '_z8PSLTydAFciEMziLSSlg'
        })
        //
        this.defaultLayers = this.platform.createDefaultLayers()
        //  初始化一张地图,设置中心点
        this.map = new H.Map(mapContainer,this.defaultLayers.normal.map)
        // 默认，实现泛/缩放。
      this.behavior = new H.mapevents.Behavior(new H.mapevents.MapEvents(this.map))
        // 创建默认UI组件
      this.ui = H.ui.UI.createDefault( this.map, this.defaultLayers)
        // 创建地图
        this.mapFn(this.map)
      },
      // 城市列表
      cityFn () {
        var ulr = '/api/cityList'
        this.$http.get(ulr, {
          headers: {
          }
        }).then(function (res) {
        })
      },
      // 计算和显示从点到b点的路线。
      calculateRouteFromAtoB (platform) {
        // 地图计算路径api
        var router = platform.getRoutingService(),
          // parm
          routeRequestParams = {
            mode: 'fastest;car',
            representation: 'display',
            routeattributes : 'waypoints,summary,shape,legs',
            maneuverattributes: 'direction,action',
            waypoint0: '52.5160,13.3779', // Brandenburg Gate
            waypoint1: '52.5206,13.3862'  // Friedrichstraße Railway Station
          };
        router.calculateRoute(
          routeRequestParams,
          this.onSuccess,
          this.onError
        )
      },
      /** 绘画路径
       * Creates a H.map.Polyline from the shape of the route and adds it to the map.
       * @param {Object} route A route as received from the H.service.RoutingService
       */
      addRouteShapeToMap (route) {
        //
        var lineString = new H.geo.LineString()
          // 从shape绘画路径
//          routeShape = route.shape,
        var routeShape = route
        var  polyline
        // 轮询解析坐标
        routeShape.forEach(function (point) {
//          var parts = point.split(',')
          // 路径坐标收集
          lineString.pushLatLngAlt(point.lat, point.lng)
        })
        // 创建完整路径
        polyline = new H.map.Polyline(lineString, {
          style: {
            lineWidth: 4,
            strokeColor: 'rgba(0, 128, 255, 0.7)'
          }
        })
        // 将路径添加到地图
        this.map.addObject(polyline)
        // And zoom to its bounding rectangle/自适应显示路径
        this.map.setViewBounds(polyline.getBounds(), true)
      },
      /** 创建地图点maneuver
       * Creates a series of H.map.Marker points from the route and adds them to the map.
       * @param {Object} route  A route as received from the H.service.RoutingService
       */
      addManueversToMap (route) {
        var that = this
        // 定义标注样式
        var svgMarkup = '<svg width="18" height="18" ' +
          'xmlns="http://www.w3.org/2000/svg">' +
          '<circle cx="8" cy="8" r="8" ' +
          'fill="#0077ff" stroke="white" stroke-width="1"  />' +
          '</svg>',
          svgMarkup1 = '<svg width="18" height="18" ' +
            'xmlns="http://www.w3.org/2000/svg">' +
            '<circle cx="9" cy="9" r="8" ' +
            'fill="#0077ff"  stroke="#000" stroke-width="3"  />' +
            '</svg>',
           dotIcon,
          group = new  H.map.Group()
        for (let i in route) {
          if(i==0){
            dotIcon = new H.map.Icon(svgMarkup1, {anchor: {x: 9, y: 9}})
          }else {
            dotIcon = new H.map.Icon(svgMarkup, {anchor: {x: 8, y: 8}})
          }
          // Add a marker to the maneuvers group 创建标注
          var marker = new H.map.Marker({lat: route[i].lat, lng: route[i].lng}, {icon: dotIcon})
          // 添加标注描述
          // marker.instruction = '第' + j + '个'
          marker.instruction = '<p style="font-size: 14px;text-align: left">当前:' + route[i].location + '</p>'
          marker.instruction += '<p style="font-size: 12px;text-align: left">更新时间:' + route[i].ymd + route[i].hms + '</p>'
          // 将marker注入地图显示
          group.addObject(marker)
          if (i == route.length-1) {
//            that.map.setCenter({lat: route[i].lat, lng: route[i].lng})
            this.openBubble({lat: route[i].lat, lng: route[i].lng}, marker.instruction)
          }
        }
        // 添加事件监听
        group.addEventListener('tap', function (evt) {
          that.map.setCenter(evt.target.getPosition())
          // 生成弹框
          that.openBubble(
            evt.target.getPosition(), evt.target.instruction)
        }, false)
        // Add the maneuvers group to the map
        this.map.addObject(group)
      },
      // 地图api计算成功后的回掉
      onSuccess (result) {
        // 路径点
        var route = result.response.route[0]
        /*
         * The styling of the route response on the map is entirely under the developer's control.
         * A representitive styling can be found the full JS + HTML code of this example
         * in the functions below:
         */
        this.addRouteShapeToMap(route)
        this.addManueversToMap(route)
//        this.addWaypointsToPanel(route.waypoint);
//        this.addManueversToPanel(route);
//        this.addSummaryToPanel(route.summary);
        // ... etc.
      },
      onError () {
        alert('Ooops!')
      },
      // 标注弹框
      openBubble (position,text) {
        // 有无弹框
        if (!this.bubble) {
          // 没有新建
          this.bubble = new H.ui.InfoBubble(
            position,
            // The FO property holds the province name.弹框类容
            {content: text, style: 'color:#999'})
          // 再ui组件中添加弹框
          this.ui.addBubble(this.bubble)
        } else {
          // 从新赋值
          this.bubble.setPosition(position)
          this.bubble.setContent(text)
          // 从新打开，刷新数
          this.bubble.open()
        }
      },
      saechFn () {
        this.showBox = !this.showBox
         //传入搜索条件
        var str = this.input
        str = str.replace(/(^\s*)|(\s*$)/g, "")
        if (!str) {
          return false
        }
        var parm = {
          code: this.input
        }
        // 清空 vuex 列表
        this.$store.commit('transportListTo', null)
        if (this.user.id) {
          this.checkNumber(parm)
        } else {
          this.checkID(parm)
        }
      },
      // 列表查询
      listFn (id) {
        var parm = {
          id: id
        }
        this.showBox = !this.showBox
        // 清空 vuex 地图列表
        this.$store.commit('addMapList', null)
        // 查询
        this.mapListpost(parm)
      },
      // 点开列表
      opentList () {
        var dom = document.getElementById('ul'),
          H = dom.scrollHeight,i=0,
          dom2 = document.getElementById('sty'),
          str = ''
        // 判断是否ul收起
        if ( !this.clickStatus) {
          // 判断ul高度，给出超出隐藏
          if (H > 300) {
            dom.style = 'height：300px;overflow-y:scroll'
            H = 300
          }
          // 加载动画
          str += '#bodyBox #ul{ height:0px; animation: myA 1s forwards;  } @keyframes myA {0%{height: 0px;} 100%{height: '+ H +'px;}';
          dom2.innerHTML = str
          this.clickStatus = true
          // 区分语言 给出点击提示
          if (this.languageCheak == 'Chinese') {
            this.clickText = '点击收起'
          }
          if (this.languageCheak == 'English') {
            this.clickText =  'Fold'
          }
          return false
        } else {
          // 收起状态
          var dom = document.getElementById('ul'),
            dom2 = document.getElementById('sty')
            str+=''
          dom2.innerHTML = str
          this.clickStatus = false
          if (this.languageCheak == 'Chinese') {
            var le = this.ULlist.listCnut - 2
            this.clickText = '共 ' + le + ' 个位置'
          }
          if (this.languageCheak == 'English') {
            var le = this.ULlist.listCnut - 2
            this.clickText = le + ' tracking event'
          }
        }
      },
      // 解析地图数据
      mapListDate (res) {
        res = res.body.data
        var pushList = []  // 经纬度地图列表
        var startTime = { ymd: '', hms: '' }
        if (res.startTime) {
          startTime = Public.publicLo.timeCheng(res.startTime)
        }
        var chart = {
          containerId: res.containerId,
          transportStatus: res.transportStatus,
          deviceNumber: res.deviceNumber,
          startTime: startTime.ymd + startTime.hms
        }
        // 基础信息
        this.dataChart = chart
        var ulList = {
          first: null, // 开始地址
          list: [],   // 中间地址
          last:  null, //  第二次地址
          listCnut: null
        } // 运输ul列表
        res = res.tracks
        var leng = res.length
        if (res.length > 0) {
          for (let i in res) {
            var time = Public.publicLo.timeCheng(res[i].time)
            var pu = {
              lat: res[i].latitude, // 经度
              lng: res[i].longitude, // 纬度
              ymd: time.ymd,   // 日期
              hms: time.hms,   // 时间
              location: res[i].location || '异常位置' + i // 地址信息
            }
            if (i == 0) {
              // 导入第一条 ul显示数据
              ulList.first = pu
              ulList.last= {
                lat: null, // 经度
                  lng: null, // 纬度
                  ymd: null,   // 日期
                  hms: null,   // 时间
                  location: null // 地址信息
              } //  第二次地址
              // 创建地点中心图
              if(this.map){
                this.mapFn(this.map, pu)
              }
            }
            if (i >= leng - 1 && leng > 1) {
              // 导入最后一条ul的显示数据
              ulList.last = pu
              ulList.listCnut = leng
            }
            if (i > 0 && i < leng - 1) {
              // 导入最后ul中间的
              ulList.list.push(pu)
            }
            // 组装路径和标注点
            pushList.push(pu)
          }
         this.ULlist = ulList
          // 绘画轨迹
          if (pushList.length > 1) {
            this.addRouteShapeToMap(pushList)
          }
          // 绘画标注
          this.addManueversToMap(pushList)
        }
        // 判断中英文
        if (this.languageCheak == 'Chinese') {
          var le = this.ULlist.listCnut - 2
          this.clickText = '共 ' + le + ' 个位置'
        }
        if (this.languageCheak == 'English') {
          var le = this.ULlist.listCnut - 2
          this.clickText = le + ' tracking event'
        }
      },
      // 点击地点切换弹框
      updateMap (list) {
        var instruction = '<p style="font-size: 14px;text-align: left">当前:' + list.location + '</p>'
             instruction += '<p style="font-size: 12px;text-align: left">更新时间:' + list.ymd + list.hms + '</p>'
        this.map.setCenter({lat: list.lat, lng: list.lng})
        this.openBubble({lat: list.lat, lng: list.lng}, instruction)
      }
//      addWaypointsToPanel (waypoints) {
//        var nodeH3 = document.createElement('h3')
//        var waypointLabels = []
//        for (let i = 0; i < waypoints.length; i += 1) {
//          waypointLabels.push(waypoints[i].label)
//        }
//        nodeH3.textContent = waypointLabels.join(' - ')
//        this.routeInstructionsContainer.innerHTML = ''
//        this.routeInstructionsContainer.appendChild(nodeH3)
//      }
//      addSummaryToPanel(summary){
//        var summaryDiv = document.createElement('div'),
//          content = '';
//        content += '<b>Total distance</b>: ' + summary.distance  + 'm. <br/>';
//        content += '<b>Travel Time</b>: ' + summary.travelTime.toMMSS() + ' (in current traffic)';
//
//
//        summaryDiv.style.fontSize = 'small';
//        summaryDiv.style.marginLeft ='5%';
//        summaryDiv.style.marginRight ='5%';
//        summaryDiv.innerHTML = content;
//        this.routeInstructionsContainer.appendChild(summaryDiv);
//      },
